<template>
  <a-modal 
    :maskClosable="false" 
    :title="$t('common.detail')" 
    :width="1000" 
    v-model="isShow"
    @ok="onSubmit"
    @cancel="cancel" 
    :destroyOnClose="true">
    <div class="table-page-search-wrapper">
      <a-tabs default-active-key="1" @change="callback" :activeKey="ruleType">
        <a-tab-pane key="1" :tab="this.$t('loss.select')">
          <a-form layout="inline" style="margin-top:10px">
            <a-row :gutter="20">
              <a-col :md="9" :sm="24" :offset="1">
                <a-form-item :label="this.$t('device.index4')">
                  <a-select v-model="queryParam.meterSort" style="width: 90%" :allowClear="true">
                    <a-select-option value="0"> {{ $t('dct.METER_SORT0') }} </a-select-option>
                    <a-select-option value="1"> {{ $t('dct.METER_SORT1') }} </a-select-option>
                    <a-select-option value="2"> {{ $t('dct.METER_SORT2') }} </a-select-option>
                    <a-select-option value="3"> {{ $t('dct.METER_SORT3') }} </a-select-option>
                    <a-select-option value="4"> {{ $t('dct.METER_SORT4') }} </a-select-option>
                    <a-select-option value="7"> {{ $t('dct.METER_SORT7') }} </a-select-option>
                  </a-select>
                </a-form-item>
              </a-col>
              <a-col :md="9" :sm="24">
                <a-form-item :label="this.$t('dataedit.device_num')">
                  <a-input v-model="queryParam.deviceNum" style="width: 90%" />
                </a-form-item>
              </a-col>
              <a-col :md="3" :sm="48">
                <span class="table-page-search-submitButtons">
                  <btn-search @click="onSearch" :showInput="false"></btn-search>
                </span>
              </a-col>
            </a-row>
          </a-form>
          <s-table  
            ref="table"
            :bordered="false"
            :autoLoad="true"
            :data="loadData"
            size="small"
            rowKey="deviceId"
            :rowSelection="{type:'checkbox', fixed: false, onChange: onSelect}"
          >
            <a-table-column 
              :title="this.$t('task.index1')"
              width="50px"
              align="center"
            >
              <span slot-scope="text">
                <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
              </span>
            </a-table-column>
            <a-table-column
              :title="this.$t('dataedit.device_num')"
              width="150px"
              dataIndex="deviceNum"
            >
            </a-table-column>
            <a-table-column
              :title="this.$t('meter.index1')"
              width="150px"
              dataIndex="meterName"
            >
            </a-table-column>
            <a-table-column
              :title="this.$t('common.vol_level')"
              width="150px"
              dataIndex="volLevel"
            >
              <span slot-scope="text,record">
                <span>{{ $t('dct.VOL_LEVEL'+record.volLevel) }}</span>
              </span>
            </a-table-column>
            <a-table-column
              :title="this.$t('device.dire')"
              width="150px"
            >
              <span slot-scope="text,record">
                <a-select v-model="record.meteringDire" style="width: 90%" :disabled="record.supNum>0">
                  <a-select-option value="1"> {{ $t('loss.mdire0') }} </a-select-option>
                  <a-select-option value="0"> {{ $t('loss.mdire1') }} </a-select-option>
                </a-select>
              </span>
            </a-table-column>
            <a-table-column
              :title="this.$t('loss.supply')"
              width="150px"
            >
              <span slot-scope="text,record">
                <a-select v-model="record.supplyDire" style="width: 90%" :disabled="record.supNum>0">
                  <a-select-option value="1"> {{ $t('loss.dire0') }} </a-select-option>
                  <a-select-option value="0"> {{ $t('loss.dire1') }} </a-select-option>
                </a-select>
              </span>
            </a-table-column>
          </s-table>
        </a-tab-pane>
        <a-tab-pane key="2" :tab="this.$t('loss.selected')">
          <a-form layout="inline" style="margin-top:10px">
            <a-row :gutter="20">
              <a-col :md="9" :sm="24" :offset="1">
                <a-form-item :label="this.$t('loss.supply')">
                  <a-select v-model="queryParam.supplyDire" style="width: 90%" :allowClear="true">
                    <a-select-option value="0"> {{ $t('loss.dire0') }} </a-select-option>
                    <a-select-option value="1"> {{ $t('loss.dire1') }} </a-select-option>
                  </a-select>
                </a-form-item>
              </a-col>
              <a-col :md="9" :sm="24">
                <a-form-item :label="this.$t('dataedit.device_num')">
                  <a-input v-model="queryParam.deviceNum" style="width: 90%" />
                </a-form-item>
              </a-col>
              <a-col :md="3" :sm="48">
                <span class="table-page-search-submitButtons">
                  <btn-search @click="onSearch" :showInput="false"></btn-search>
                </span>
              </a-col>
            </a-row>
          </a-form>
          <s-table  
            ref="table1"
            :bordered="false"
            :autoLoad="true"
            :data="loadData"
            size="small"
            rowKey="deviceId,supplyDire"
          >
            <a-table-column 
              :title="this.$t('task.index1')"
              width="50px"
              align="center"
            >
              <span slot-scope="text">
                <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
              </span>
            </a-table-column>
            <a-table-column
              :title="this.$t('dataedit.device_num')"
              width="150px"
              dataIndex="deviceNum"
            >
            </a-table-column>
            <a-table-column
              :title="this.$t('meter.index1')"
              width="150px"
              dataIndex="meterName"
            >
            </a-table-column>
            <a-table-column
              :title="this.$t('common.vol_level')"
              width="150px"
              dataIndex="volLevel"
            >
              <span slot-scope="text,record">
                <span>{{ $t('dct.VOL_LEVEL'+record.volLevel) }}</span>
              </span>
            </a-table-column>
            <a-table-column
              :title="this.$t('device.dire')"
              width="150px"
            >
              <span slot-scope="text,record">
                <a-select v-model="record.meteringDire" style="width: 90%" :disabled="true">
                  <a-select-option value="0"> {{ $t('loss.mdire0') }} </a-select-option>
                  <a-select-option value="1"> {{ $t('loss.mdire1') }} </a-select-option>
                </a-select>
              </span>
            </a-table-column>
            <a-table-column
              :title="this.$t('loss.supply')"
              width="150px"
            >
              <span slot-scope="text,record">
                <a-select v-model="record.supplyDire" style="width: 90%" :disabled="true">
                  <a-select-option value="0"> {{ $t('loss.dire0') }} </a-select-option>
                  <a-select-option value="1"> {{ $t('loss.dire1') }} </a-select-option>
                </a-select>
              </span>
            </a-table-column>
            <a-table-column
              :title="this.$t('task.action')"
              dataIndex="action"
            >
              <span slot-scope="text,record">
                <btn-remove @click="onRemove(record)" type="a" ></btn-remove>
              </span>
            </a-table-column>
          </s-table>
        </a-tab-pane>
      </a-tabs>
    </div>
  </a-modal>
</template>

<script>
  import STable from '@/components/table/'
  import Dict from '@/views/public/Dict'
  import {
    axios
  } from '@/utils/request'
  export default {
    components: {
      STable,
      Dict, 
    },
    data() {
      return {
        data: {},
        isShow: false,
        selectedRows:[],
        ruleType:'1',
        sort:'',
        queryParam: {meterSort:'',supplyDire:''},
        loadData: (parameter) => {    
          const that = this  
          var url='loss/model/four/search-meter-in'
          if(that.ruleType=='1'){
             parameter.deptId =that.params.deptId 
             parameter.sort =that.sort
             url='loss/model/four/search-meter'
          }else{
             parameter.id=this.params.id
          }
          return axios({
            method: 'get',
            url: url,
            params: Object.assign(parameter, this.queryParam)
          }).then(res => {
            return res.result
          })
        }
      }
    },
    methods: {
      callback(key){
        this.ruleType=key
        if(this.ruleType==1){
           this.$refs.table.refresh()
        }else{
          this.$refs.table1.refresh()
        }
      },
      showModal(params,type) {
        this.isShow = true
        this.ruleType='1'
        this.params=params
        this.sort=type
        this.selectedRows=[]
        this.selectedRowKeys=[]
        this.queryParam={meterSort:'',supplyDire:''}
      },
      onSearch() {
        if(this.ruleType==1){
           this.$refs.table.refresh()
        }else{
          this.$refs.table1.refresh()
        }
      },
      onSubmit () {   
        if(this.ruleType==2) {
          this.$emit('ok',[])
          this.isShow = false
          return
        }
        if(this.selectedRows.length==0){
          this.$message.info(this.$t('common.selectone'))
          return
        }
        const adddata=[]
        this.selectedRows.forEach(item => {
          adddata.push({
            deviceId:item.deviceId,
            supplyDire:item.supplyDire!='0'?'0':'1',
            deviceNum:item.deviceNum,
            deptId:item.deptId,
            orgId:item.orgId,
            meteringDire:item.meteringDire!='0'?'0':'1',
            meterSort:item.meterSort,
            meterName:item.meterName,
            volLevel:item.volLevel
          })
        })
        axios({
          url: '/loss/model/four/insert-device',
          method: 'post',
          data: {data:JSON.stringify(adddata),id:this.params.id}
        }).then(result=>{
          if(result){
            this.$emit('ok',result)
            this.isShow = false
          }
        }) 
      },
      onRemove(record) {    
        axios({
          url: '/loss/model/four/delete-device',
          method: 'delete',
          params: {id:this.params.id,deviceId:record.deviceId,supplyDire:record.supplyDire}
        }).then(result=>{
          if(result){
            this.$refs.table1.refresh()
          }
        }) 
      },
      cancel() {
        this.isShow = false
      },
      onSelect(selectedRowKeys, selectedRows) {
        console.log(selectedRows)
        this.selectedRows = selectedRows
      },
    },
  }
</script>
