<template>
  <a-modal 
    :maskClosable="false" 
    :title="$t('common.detail')" 
    :width="800" 
    v-model="isShow"
    @ok="onSubmit"
    @cancel="cancel" 
    :destroyOnClose="true">
    <div class="table-page-search-wrapper" style="padding-bottom:40px;">
      <a-form class="ant-advanced-search-form searchFixed" layout="inline" :form="form">
        <a-row :gutter="32" v-if="data.length==1">
          <a-col :xl="12" :md="12" :sm="48" v-if="sort<3">
            <a-form-item :label="$t('dept.index')" >
              <dept-select 
                :disabled="true"
                v-decorator="[
                  'deptId',
                  {initialValue: data[0].deptId?data[0].deptId:''}
                ]" />
            </a-form-item>
          </a-col>
          <a-col :xl="12" :md="12" :sm="48" v-if="sort=='1'">
            <a-form-item :label="$t('common.vol_level')" >
              <span>{{ $t('dct.VOL_LEVEL'+data[0].volCode) }}</span>
            </a-form-item>
          </a-col>
          <a-col :xl="12" :md="12" :sm="48" v-if="sort=='2'||sort=='3'">
            <a-form-item :label="$t('line.name')" >
              <span>{{ data[0].lineName }}</span>
            </a-form-item>
          </a-col>
          <a-col :xl="12" :md="12" :sm="48" v-if="sort=='3'">
            <a-form-item :label="$t('loss.tmname')" >
              <span>{{ data[0].tmName }}</span>
            </a-form-item>
          </a-col>
          <a-col :xl="12" :md="12" :sm="48" v-if="sort>3">
            <a-form-item :label="$t('line.info1')" >
              <span>{{ data[0].stationName }}</span>
            </a-form-item>
          </a-col>
          <a-col :xl="12" :md="12" :sm="48" v-if="sort=='4'">
            <a-form-item :label="$t('line.name1')" >
              <span>{{ data[0].lineName }}</span>
            </a-form-item>
          </a-col>
          <a-col :xl="12" :md="12" :sm="48" v-if="sort=='5'">
            <a-form-item :label="$t('maintransformer.name')" >
              <span>{{ data[0].stationMtName }}</span>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="32" style="margin-top:20px;">
          <a-col :xl="12" :md="12" :sm="48">
            <a-form-item :label="$t('loss.index6')" >
              <a-input 
                v-decorator="[
                  'lossIndexDay', 
                  {initialValue: data[0].lossIndexDay?data[0].lossIndexDay:'5', rules: [{required: true,message:$t('loss.index6')+' '+$t('common.notnull')}] }
                ]"
              />
            </a-form-item>
          </a-col>
          <a-col :xl="12" :md="12" :sm="48">
            <a-form-item :label="$t('loss.index7')" >
              <a-input 
                v-decorator="[
                  'lossIndexMonth', 
                  {initialValue: data[0].lossIndexMonth?data[0].lossIndexMonth:'5', rules: [{required: true,message:$t('loss.index7')+' '+$t('common.notnull')}] }
                ]"
              />
            </a-form-item>
          </a-col>
        </a-row>
      </a-form>
    </div>
  </a-modal>
</template>

<script>
  import {
    axios
  } from '@/utils/request'
  import DeptSelect from '@/views/public/DeptSelect'
  export default {
    components: {
      DeptSelect
    },
    data() {
      return {
        isShow: false,
        data:[{}],
        sort:'',
        form: this.$form.createForm(this),
      }
    },
    methods: {
      showModal(params,type) { 
        this.isShow = true
        this.sort=type
        setTimeout(()=>{
          this.data=params?params:[]
          
        },200)
        
      },
      onSubmit () {  
        this.form.validateFields((err,values) => {     
          const that = this
          const obj=[]
          that.data.forEach(item => {
            obj.push({
              id:item.id,
              lossIndexDay:values.lossIndexDay,
              lossIndexMonth:values.lossIndexMonth
            })
          })
          console.log(obj)
          axios({
            url: '/loss/model/four/update',
            method: 'post',
            data:  {data:JSON.stringify(obj)}
          }).then(result=>{
            if(result){
              this.$emit('ok',result)
              this.isShow = false
            }
          })  
        }) 
      },
      cancel() {
        this.isShow = false
      },
    },
  }
</script>
